<template>
  <div
    v-if="item"
    key="product"
    class="product">
    <h1></h1>
    <!-- route -->
    <!--<p>ID.{{ this.$route.params.id }}</p>-->
    <!-- routerpropstrue -->
    <p>ID.{{ id }}</p>

    <dl class="product-table">
      <dt></dt>
      <dd>{{ item.name }}</dd>
      <dt></dt>
      <dd>{{ item.price }}</dd>
      <dt></dt>
      <dd>{{ item.content }}</dd>
    </dl>
  </div>
  <div
    v-else
    key="loading"></div>
</template>

<script>
import products from "../api/products";

export default {
  name: "Product",
  // routerpropstrue
  props: {
    id: {
      type: Number,
      default: 0
    }
  },
  data() {
    return {
      item: null
    };
  },
  watch: {
    id: {
      handler() {
        products.asyncFind(this.id, item => {
          this.item = item;
        });
      },
      immediate: true
    }
  }
};
</script>
