const database = [
  { id: 1, name: "A", price: 100, content: "A" },
  { id: 2, name: "B", price: 200, content: "B" },
  { id: 3, name: "C", price: 300, content: "C" }
];

export default {
  fetch() {
    return database;
  },
  find(id) {
    return database.find(el => el.id === id);
  },
  asyncFind(id, callback) {
    setTimeout(() => {
      callback(database.find(el => el.id === id));
    }, 1000);
  }
};
